r\n  background: linear-gradient(to right, #2F435A, #8195ac);\r\n  color: #FDCE6D;\r\n}\r\n\r\n/* configuração barra de navegação lateral oculta */\r\n\r\n\r\n.slide {\r\n  height: 100%;\r\n  width: 250px;\r\n  position: fixed;\r\n  background-color: #FDCE6D;\r\n  transition: 0.5s ease;\r\n  transform: translateX(-250px);\r\n}\r\n\r\nul li {\r\n  list-style: none;\r\n}\r\n\r\n.menu {\r\n  margin-top: 64px;\r\n}\r\n\r\nul li a {\r\n  padding: 16px;\r\n  display: block;\r\n  text-decoration: none;\r\n  text-transform: capitalize;\r\n  transition: 0.2s ease-out;\r\n  color: #2F435A;\r\n\r\n}\r\n\r\nul li:hover a {\r\n  color: #fff;\r\n  background-color: #2F435A;\r\n}\r\n\r\nul li a i {\r\n  width: 40px;\r\n  text-align: center;\r\n\r\n}\r\n\r\n\r\n.inputMenu {\r\n  display: none;\r\n  visibility: hidden;\r\n}\r\n\r\n.toggle {\r\n  position: fixed;\r\n  height: 16px;\r\n  width: 100%;\r\n  top: 0px;\r\n  left: 0px;\r\n  padding-top: 16px;\r\n  z-index: 1;\r\n  cursor: pointer;\r\n  border-radius: 2px;\r\n  background-color: 0;\r\n}\r\n\r\n.toggle i {\r\n  font-size: 30px;\r\n  margin-left: 16px;\r\n}\r\n\r\n\r\n.inputMenu:checked~.slide {\r\n  transform: translateX(0);\r\n}\r\n\r\n\r\n\r\n/* Cabeçalho */\r\n\r\n\r\nheader.topo section.cabecalho {\r\n  display: flex;\r\n  flex-direction: row;\r\n  align-items: center;\r\n  max-height: 1000px;\r\n  padding: 64px;\r\n}\r\n\r\nheader.topo section.cabecalho .logo img {\r\n  width: 500px;\r\n  margin-right: 32px;\r\n\r\n}\r\n\r\nheader.topo section.cabecalho .textlogo {\r\n  text-align: right;\r\n  margin-left: 64px;\r\n  margin-right: 32px;\r\n\r\n}\r\n\r\n/* Sessão de agendamento*/\r\n\r\n.container1 {\r\n  display: flex;\r\n  justify-content: center;\r\n  padding: 64px;\r\n  background-color: 0;\r\n  width: 100%;\r\n}\r\n\r\n.card {\r\n  display: flex;\r\n  flex-direction: row;\r\n  align-items: center;\r\n  margin-right: 32px;\r\n  width: 60%;\r\n  height: 200px;\r\n  border-radius: 15px;\r\n  background-color: #FDCE6D4D;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)\r\n}\r\n\r\n.fototosa {\r\n  width: 150px;\r\n  height: 160px;\r\n  border-radius: 8px;\r\n  margin: 10px;\r\n}\r\n\r\n.textoagendamento {\r\n  height: 160px;\r\n  width: 80%;\r\n  padding: 0px;\r\n  /* max-width: 60%; */\r\n}\r\n\r\n/* Sessão Quem Somos*/\r\n\r\nsection.quemsomos {\r\n  display: flex;\r\n  flex-direction: row;\r\n  text-align: left;\r\n  padding: 64px;\r\n  gap: 64px;\r\n}\r\n\r\n\r\nsection.quemsomos .textoquem {\r\n  display: flex;\r\n  text-align: left;\r\n  align-self: center;\r\n}\r\n\r\nsection.quemsomos .foto img {\r\n\r\n  width: 350px;\r\n  margin-right: 64px;\r\n  border-radius: 10px;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)\r\n}\r\n\r\n/* Sessão Depoimentos*/\r\n\r\n.grid1 {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1fr;\r\n  max-width: 100%;\r\n  font-size: 14px;\r\n  padding: 64px;\r\n  gap: 32px;\r\n}\r\n\r\n.grid1>div:nth-child(n + 1) {\r\n  display: grid;\r\n  grid-template-columns: 1fr 2fr;\r\n  align-items: center;\r\n  padding-right: 32px;\r\n}\r\n\r\n#imgdepo {\r\n  width: 250px;\r\n}\r\n\r\n\r\n\r\n/* Sessão Solicitação de Suporte*/\r\n\r\n\r\n.container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: start;\r\n  align-items: start;\r\n  background-color: 0;\r\n  padding-left: 32px;\r\n  margin-bottom: 64px;\r\n\r\n\r\n\r\n}\r\n\r\n.form_box {\r\n  display: flex;\r\n  flex-direction: column;\r\n  width: 500px;\r\n\r\n}\r\n\r\n.form_box form {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: start;\r\n  flex-wrap: flex;\r\n  padding: 16px;\r\n  margin-left: 16px;\r\n}\r\n\r\n.form_box input,\r\n.form_box textarea {\r\n  width: calc(50% - 8px);\r\n  height: 36px;\r\n  border-radius: 8px;\r\n  border: none;\r\n  padding-left: 16px;\r\n  margin-bottom: 16px;\r\n  font-size: 16px;\r\n  background-color: #FDCE6D4D;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)\r\n}\r\n\r\n\r\n.form_box input:first-child {\r\n  margin-right: 16px;\r\n\r\n}\r\n\r\n.form_box input {\r\n  width: 100%;\r\n\r\n}\r\n\r\n.form_box textarea {\r\n  width: 100%;\r\n  height: 100px;\r\n  padding: 16px;\r\n\r\n}\r\n\r\n\r\n/* Sessão Redes sociais */\r\n\r\n.grid2 {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1fr;\r\n  background-color: 0;\r\n\r\n}\r\n\r\n.grid2 img {\r\n  margin-right: 24px;\r\n\r\n\r\n}\r\n\r\n.grid2 .redessociais {\r\n  margin-left: 20%;\r\n  margin-top: 10%;\r\n\r\n\r\n}\r\n\r\n/* Responsividade para tela grandes - Notebook 1024 ------------------------------------------------------------------*/\r\n\r\n\r\n\r\n@media only screen and (max-width: 1025px){\r\n\r\n  /* Cabeçalho */\r\n\r\n\r\nheader.topo section.cabecalho {\r\n  display: flex;\r\n  flex-direction: row;\r\n  align-items: center;\r\n  max-height: 1000px;\r\n  padding: 64px;\r\n}\r\n\r\nheader.topo section.cabecalho .logo img {\r\n  width: 500px;\r\n  margin-right: 32px;\r\n\r\n}\r\n\r\nheader.topo section.cabecalho .textlogo {\r\n  text-align: right;\r\n  margin-left: 64px;\r\n  margin-right: 32px;\r\n\r\n}\r\n\r\n/* Sessão de agendamento*/\r\n\r\n.container1 {\r\n  display: flex;\r\n  justify-content: center;\r\n  padding: 64px;\r\n  background-color: 0;\r\n  width: 100%;\r\n}\r\n\r\n.card {\r\n  display: flex;\r\n  flex-direction: row;\r\n  align-items: center;\r\n  margin-right: 32px;\r\n  width: 60%;\r\n  height: 230px;\r\n  border-radius: 15px;\r\n  background-color: #FDCE6D4D;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)\r\n}\r\n\r\n.fototosa {\r\n  width: 150px;\r\n  height: 160px;\r\n  border-radius: 8px;\r\n  margin: 10px;\r\n}\r\n\r\n.textoagendamento {\r\n  height: 180px;\r\n  width: 60%;\r\n  padding-left: 8px;\r\n\r\n  /* max-width: 60%; */\r\n}\r\n\r\n/* Sessão Quem Somos*/\r\n\r\nsection.quemsomos {\r\n  display: flex;\r\n  flex-direction: row;\r\n  text-align: left;\r\n  p